
import { mapMutations } from 'vuex';
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
name:'imgmore',
props:['carid'],
data:function(){
return{
pageid:1,
list:[],
pullupConfig: {
content: "上拉加载更多",
downContent: "松开进行加载",
upContent: "上拉加载更多",
loadingContent: "加载中...",
},
}
},
components: {
Scroller
},
methods:{
...mapMutations(['mutationImgID']),
loadMoreImgs:function(){
var app = this;
this.pageid++;
this.$http.get('/v2-car-getCategoryImageList.html?SerialID=' + app.carid + '&ImageID=' + app.$store.state.imgid + '&Page=' + app.pageid + '&PageSize=30&_1531054352382').then(function(response){
for (var i = 0; i < response.data.data.List.length; i++){
app.list.push(response.data.data.List[i]);
}


app.$refs.pullup.donePullup();
})
}
},
beforeRouteEnter:function(to, from, next){
next(function(vm){
vm.mutationImgID(window.localStorage.getItem('imgid'));
vm.$http.get('/v2-car-getCategoryImageList.html?SerialID=' + vm.carid + '&ImageID=' + vm.$store.state.imgid + '&Page=' + vm.pageid + '&PageSize=30&_1531054352382').then(function(response){

vm.list = response.data.data.List;
})
})

}
}



