
import Card from 'vux/src/components/card/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default {
name:'car',
props:['id'],
components:{
Card,
Tab,
TabItem
},
data:function(){
return{
car:'',
carshow:[],
imgCarID:'',
show:'all'
}
},
methods:{

toprice:function(carid){
this.$router.push('/price/' + carid);
},

toimgs:function(){
this.$router.push('/imgs/' + this.imgCarID);
},

showAll:function(){
var app = this;
this.$http.get('/v2-car-getInfoAndListById.html?SerialID=' + this.id).then(function(response){
app.carshow = response.data.data.list;
})
},

show2019:function(){
var app = this;
this.$http.get('/v2-car-getInfoAndListById.html?SerialID=' + this.id).then(function(response){
var arr = [];
for(var i = 0;i < response.data.data.list.length;i++){
if(response.data.data.list[i].market_attribute.year == "2019"){
arr.push(response.data.data.list[i]);
}
}
app.carshow = arr;
})
},

show2018:function(){
var app = this;
this.$http.get('/v2-car-getInfoAndListById.html?SerialID=' + this.id).then(function(response){
var arr = [];
for(var i = 0;i < response.data.data.list.length;i++){
if(response.data.data.list[i].market_attribute.year == "2018"){
arr.push(response.data.data.list[i]);
}
}
app.carshow = arr;
})
},
},
beforeRouteEnter:function(to, from ,next){
next(function(vm){
vm.$http.get("/v2-car-getInfoAndListById.html?SerialID=" + to.params.id).then(function(response){
vm.imgCarID = response.data.data.SerialID;
vm.car = response.data.data;
vm.carshow = response.data.data.list;
});
});
},
beforeRouteUpdate:function(to, from, next){
var app = this;
this.$http.get("/v2-car-getInfoAndListById.html?SerialID=" + to.params.id).then(function(response){
app.imgCarID = response.data.data.SerialID;
app.car = response.data.data;
app.carshow = response.data.data.list;
});
next();
}
}



