
import Card from 'vux/src/components/card/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
name:'price',
props:['carid'],
data:function(){
return{
cityid:'',
cityname:'',
car:'',
list:[],
check:[]
}
},
components:{
Card,
XInput,
XButton,
Group,
Cell,
CheckIcon,
Scroller
},
beforeRouteEnter:function(to, from, next){
next(function(vm){
vm.$http.get('/location-client.html?').then(function(response){

vm.cityid = response.data.data.CityID;
vm.cityname = response.data.data.CityName;
vm.$http.get('/v2-dealer-alllist.html?carId=' + to.params.carid + '&cityId=' + vm.cityid).then(function(response){

vm.car = response.data.data;
vm.list = response.data.data.list;
for(var i = 0;i<response.data.data.list.length;i++){
vm.check.push(false)
}
})
});
})
},
beforeRouteUpdate:function(to, from, next){
var app = this;
this.$http.get('/location-client.html?').then(function(response){

app.cityid = response.data.data.CityID;
app.$http.get('/v2-dealer-alllist.html?carId=' + app.carid + '&cityId=' + app.cityid).then(function(response){

app.car = response.data.data;
app.list = response.data.data.list;
for(var i = 0;i<response.data.data.list.length;i++){
app.check.push(false)
}
})
});
}
}



