import Vue from 'vue'
import VueRouter from 'vue-router'
import List from '@/components/List.vue'
import Car from '@/components/Car.vue'
import Price from '@/components/Price.vue'
import Imgs from '@/components/Imgs.vue'
import ImgMore from '@/components/ImgMore.vue'

// 全局注册路由
Vue.use(VueRouter);

// 定义路由信息对象数组
const routes = [
  // 列表
  { path:'/',name:'list',component:List },
  { path:'/car/:id',name:'car',component:Car,props:true },
  { path:'/price/:carid',name:'price',component:Price,props:true },
  { path:'/imgs/:carid',name:'imgs',component:Imgs,props:true },
  { path:'/imgmore/:carid',name:'imgmore',component:ImgMore,props:true }
]

// 创建路由管理器
const router = new VueRouter({
  routes
});

// 对外输出模块
export default router;


