
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'


export default {
name:'list',
components:{
Group,
Cell,
Popup
},
data:function(){
return{
lists:[],
letters:['A','B','C','D','F','G','H','J','K','L','M','N','O','Q','R','S','T','W','X','Y','Z'],
carid:'',
carsArr:[],
carname:'',
show:false
}
},
methods: {
openCars:function(){
var app = this;
this.carsArr=[];
this.$http.get('/v2-car-getMakeListByMasterBrandId.html?MasterID='+this.carid).then(function(response){
var thisCarArr=response.data.data;
for( var i=0;i < thisCarArr.length ;i++ ){
for(var n=0;n < thisCarArr[i].GroupList.length;n++){
app.carsArr.push(thisCarArr[i].GroupList[n]);
}
}
if(app.show==false){
app.show=true;
}
});

}
},

beforeRouteEnter:function(to, from, next){
next(function(vm){
vm.$http.get('/v2-car-getMasterBrandList.html').then(function(response){
if(response.data){
vm.lists = response.data.data;
}
});
})
}
}



